import React from 'react'
import Grid from 'react-bootstrap/lib/Grid'
import Panel from 'react-bootstrap/lib/Panel'
import Row from 'react-bootstrap/lib/Row'
//import Pi from 'pi-sdk'
//import jQuery from 'jquery-min'
import {injectIntl} from 'react-intl'

import {setTitle} from '../lib/utils'
//import {Alert} from 'bootstrap'
//import Script from 'react-load-script'
class MyAccount extends React.Component {
  constructor(){
  super()
  this.state={

   myid:'',
   username: window.localStorage.getItem('username'),
  }

  }
 


  inputChange=()=>{
  let val=this.input.value
   this.setState({
      
      myid:val,
      

})

  }


  getInput=()=>{
      if(this.state.myid === '' || this.state.myid ===null )
      {
          window.location.href = 'account/GABT7EMPGNCQSZM22DIYC4FNKHUVJTXITUF6Y5HNIWPU4GA7BHT4GC5G'
      }
      else{
        
        window.location.href = 'account/'+this.state.myid
      }
     
  }

render() {
    const {formatMessage} = this.props.intl
    

    setTitle('MyAccount')
    return (
      <Grid>
        <Row>
          <Panel header={formatMessage({id: 'MyAccount'})}>
         
          <h4>{formatMessage({id: 'Please Input your account address:'})}</h4> 
          
          <input style={{width: '100%',padding: '12px 16px'}} type='text'  ref={input=>this.input=input}defaultValue='GABT7EMPGNCQSZM22DIYC4FNKHUVJTXITUF6Y5HNIWPU4GA7BHT4GC5G' onChange={()=>this.inputChange()} ></input>
          &nbsp; &nbsp; <br></br> 
          <h4>{formatMessage({id: 'Pi Username:'})}</h4> 
          <input style={{width: '100%',padding: '12px 16px'}} type='text'  value={this.state.username} ></input>
          &nbsp; &nbsp; <br></br> 
          <input style={{backgroundColor: '#a34ea7',color: 'white',letterspacing:'0.05em',padding: '4px 12px 4px',borderRadius:'60px'}} onClick={this.getInput.bind(this)} type='submit' ></input>
      
                 
          </Panel>
          
        </Row>
      </Grid>
    )
  }
 
}

export default injectIntl(MyAccount)



